export function addTag(tagType) {
	return {
		type : 'ADD_TAG',
		tagType : tagType
	}
}

export function addTagToTask(tag){
	return {
		type: 'ADD_TASK_TAG',
		tag:tag
	}
}

export function removeTag(tagType) {
	return {
		type : 'REMOVE_TAG',
		tagType : tagType
	}
}



